.messages .messageBox {
  background: #F3F3F3;
  border-radius: 20px;
  padding: 5px 20px;
  color: white;
  display: inline-block;
  max-width: 80%;
}

.messages .messageText {
  width: 100%;
  letter-spacing: 0;
  float: left;
  font-size: 1.1em;
  word-wrap: break-word;
}

.messages .messageText img {
  vertical-align: middle;
}

.messages .messageContainer {
  display: flex;
  justify-content: flex-end;
  padding: 0 5%;
  margin-top: 3px;
}

.messages .sentText {
  display: flex;
  align-items: center;
  font-family: Helvetica;
  color: #828282;
  letter-spacing: 0.3px;
  margin: 0;
}

.messages .pl-10 {
  padding-left: 10px;
}

.messages .pr-10 {
  padding-right: 10px;
}

.messages .justifyStart {
  justify-content: flex-start;
}

.messages .justifyEnd {
  justify-content: flex-end;
}

.messages .colorWhite {
  color: white;
}

.messages .colorDark {
  color: #353535;
}

.messages .backgroundBlue {
  background: #00b5ad;
}

.messages .backgroundLight {
  background: #F3F3F3;
}