#chat {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #1A1A1D;
}

#chat html, #chat body {
  font-family: 'Roboto', sans-serif;
  padding: 0;
  margin: 0;
}

#chat * {
  box-sizing: border-box;
}

#chat .container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #FFFFFF;
  border-radius: 8px;
  height: 60%;
  width: 35%;
}

@media (min-width: 320px) and (max-width: 480px) {
  #chat {
    height: 100%;
  }

  #chat .container {
    width: 100%;
    height: 100%;
  }
}

@media (min-width: 480px) and (max-width: 1200px) {
  #chat .container {
    width: 60%;
  }
}